
export default function(entity){

    this.scripts.snippets['aJam']['loadCheckpoint'].call(this, entity);
    this.scripts.snippets['aJam']['setInputsPerso'].call(this, entity);

    const spriteSheets = entity.get('sprites');

    spriteSheets.parts.main.source='perso-idle-right'

    spriteSheets.parts.main.animation.frame=0;


    this.scripts.snippets['aJam']['soundDie'].call(this, entity);
}
